@font-face {
  font-family: "Comfortaa";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/comfortaa-v37-cyrillic-300.woff2") format("woff2"), url("../fonts/comfortaa-v37-cyrillic-300.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Comfortaa";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/comfortaa-v37-cyrillic-regular.woff2") format("woff2"), url("../fonts/comfortaa-v37-cyrillic-regular.woff") format("woff");
  font-display: swap;
}
/*----- Основные цвета -----*/
.noUi-connect {
  background: #add0c7 !important;
}
.noUi-connects {
  background: rgba(173, 208, 199, 0.5);
}
.noUi-touch-area {
  background: beige;
}
.layout-block {
  font-family: "comfortaa", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 32px;
  line-height: 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  color: grey;
}
.calc-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 55%;
  padding: 35px;
}
.calc-form__fieldset {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 100px;
  row-gap: 50px;
  padding: 0;
  margin-bottom: 130px;
  border: 0;
}
.calc-form__label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 400px;
  padding: 35px 0;
  border: 8px solid #add0c7;
  border-radius: 20px;
  background-color: rgba(173, 208, 199, 0.1);
}
.calc-form__span {
  margin-bottom: 25px;
  text-transform: uppercase;
  text-align: center;
}
.calc-form__select-container {
  background: inherit;
  position: relative;
  border-radius: 10px;
  padding: 20px 10px;
  display: flex;
}
.calc-form__select-container::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 100px;
  width: 15px;
  height: 3px;
  background-color: #add0c7;
  transform: rotate(45deg) translateY(-50%);
  border-radius: 10px;
}
.calc-form__select-container::after {
  content: '';
  position: absolute;
  top: 68%;
  left: 100px;
  width: 15px;
  height: 3px;
  background-color: #add0c7;
  transform: rotate(-45deg) translateY(-50%) translateX(100%);
  border-radius: 10px;
}
.calc-form__month {
  font-family: "comfortaa", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  line-height: 22px;
  color: grey;
  background: initial;
  border: none;
  appearance: none;
}
.calc-form__month:focus {
  outline: none;
}
.calc-form__input {
  font-family: "comfortaa", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  line-height: 22px;
  color: grey;
}
.calc-form__input--salary,
.calc-form__input--hours {
  background: inherit;
  border: none;
  border-radius: 10px;
  padding: 20px 0;
  appearance: none;
  text-align: center;
  width: 30%;
  margin-bottom: 50px;
}
.calc-form__input:focus {
  -webkit-box-shadow: 0px 0px 26px 0px rgba(173, 208, 199, 0.8);
  -moz-box-shadow: 0px 0px 26px 0px rgba(173, 208, 199, 0.8);
  box-shadow: 0px 0px 26px 0px rgba(173, 208, 199, 0.8);
  outline: 1px solid rgba(173, 208, 199, 0.8);
}
.calc-form__slider {
  width: 50%;
}
.calc-form__button {
  font-family: "comfortaa", "Arial", sans-serif;
  font-weight: 300;
  font-style: normal;
  font-size: 36px;
  line-height: 36px;
  color: grey;
  background: inherit;
  width: 80%;
  margin: 0 auto;
  text-transform: uppercase;
  border: 8px solid #add0c7;
  padding: 20px;
  border-radius: 20px;
  transition: background-color 0.3s linear, color 0.3s linear, transform 0.01s linear;
}
.calc-form__button:hover {
  background-color: rgba(173, 208, 199, 0.6);
}
.calc-form__button:active {
  transform: scale(1.01);
}
body {
  background-color: beige;
}
.result-block {
  display: flex;
  justify-content: center;
  border-radius: 20px;
  background-color: #adb6d0;
  border: 8px solid #add0c7;
  width: 600px;
  height: 300px;
}
.result-block__cost {
  background: inherit;
  border: none;
  border-radius: 10px;
  padding: 20px 0;
  appearance: none;
  text-align: center;
  width: 30%;
}
@keyframes check {
  from {
    width: 0;
    height: 0;
  }
  to {
    width: 35px;
    height: 35px;
  }
}
.input-number {
  appearance: textfield;
}
.input-number::-webkit-outer-spin-button,
.input-number::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.night-check__control-mark {
  position: relative;
  background: inherit;
  border: none;
  border-radius: 10px;
  padding: 20px 0;
  appearance: none;
  text-align: center;
  width: 30%;
  width: 40px;
  height: 40px;
}
.night-check__input:checked + .night-check__control-mark {
  -webkit-box-shadow: 0px 0px 26px 0px rgba(173, 208, 199, 0.8);
  -moz-box-shadow: 0px 0px 26px 0px rgba(173, 208, 199, 0.8);
  box-shadow: 0px 0px 26px 0px rgba(173, 208, 199, 0.8);
  outline: 1px solid rgba(173, 208, 199, 0.8);
  transition: 0.2s ease-in;
}
.night-check__input:checked + .night-check__control-mark::before {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
  left: 50%;
  animation-name: check;
  animation-duration: 300ms;
  animation-fill-mode: forwards;
  background-color: #add0c7;
  border-radius: 10px;
}
.popup {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
}
